@import url("sprite@0.5.css");

/* Rotate icons on a 4s cycle */
/* Source: https://codepen.io/Ruddy/pen/RNRybN */
.loader {
  width: 60vw;
  height: 60px;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
}

.loader .image {
  width: 60vw;
  height: 160px;
  font-size: 40px;
  text-align: center;
  transform-origin: bottom center;
  animation: 4s rotate infinite;
  opacity: 0;
}

.loader #loaderImage {
  display: inline-block;
}

.loader span {
  display: block;
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 0;
}

@keyframes rotate {
  0% {
    transform: rotate(90deg);
  }
  10% {
    opacity: 0;
  }
  35% {
    transform: rotate(0deg);
    opacity: 1;
  }
  65% {
    transform: rotate(0deg);
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    transform: rotate(-90deg);
  }
}

/* Fade loading message text in/out on a 2s cycle */
#loaderMessage {
  color: white;
}

.fade {
  animation: fadein 2s;
}

@keyframes fadein {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
