/* Rotate icons on a 4s cycle */
/* Source: https://codepen.io/Ruddy/pen/RNRybN */
.loader {
  width: 60vw;
  height: 60px;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
}

.loader .image {
  width: 60vw;
  height: 160px;
  font-size: 40px;
  text-align: center;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
  -webkit-animation: 4s rotate infinite;
          animation: 4s rotate infinite;
  opacity: 0;
}

.loader #loaderImage {
  display: inline-block;
}

.loader span {
  display: block;
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 0;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  10% {
    opacity: 0;
  }
  35% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 1;
  }
  65% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
  }
  10% {
    opacity: 0;
  }
  35% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 1;
  }
  65% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
  }
}

/* Fade loading message text in/out on a 2s cycle */
#loaderMessage {
  color: white;
}

.fade {
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

@-webkit-keyframes fadein {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

